* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
::-webkit-scrollbar {
    width: 0;
}
.App {
    width: 100vw;
    height: 100vh;
    padding: 0 300px 100px;
    color: #eee;
    /* background-color: #111; */
    box-shadow: 0 0 20px rgba(255, 255, 255, 0.5) inset;
    overflow-y: scroll;
}
.header {
    height: 80px;
    line-height: 80px;
    font-size: 27px;
    font-weight: bold;
    font-family: 'Times New Roman', Times, serif;
    color: #000;
}
.body .tabbar {
    position: relative;
    display: flex;
    margin-bottom: 5px;
    padding-top: 10px;
    /* border-top: 1px solid #666; */
    border-bottom: 1px solid #434343;
}
.body .tabbar .tab {
    position: relative;
    width: fit-content;
    height: 40px;
    margin-right: 10px;
    padding: 0 20px;
    line-height: 35px;
    text-align: center;
    font-weight: bold;
    background-color: #000;
    border: 1px solid #7c7c7c;
    border-bottom: 2px solid #fff;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    cursor: pointer;
}
.body .tabbar .tab .close {
    transform: rotate(45deg);
    position: absolute;
    top: 0px;
    right: 0px;
    width: 15px;
    height: 15px;
    line-height: 13px;
    text-align: center;
    border-radius: 50%;
    transition: all 0.3s;
}
.body .tabbar .tab .close:hover {
    color: #111;
    background-color: #fff;
    cursor: pointer;
}
.body .tabbar .new {
    position: absolute;
    top: 20px;
    right: 5px;
    width: 20px;
    height: 20px;
    line-height: 16px;
    text-align: center;
    background-color: #000;
    border: 1px solid #d3dce6;
    border-radius: 4px;
}
.body .tabbar .new:hover {
    filter: brightness(1.2);
    cursor: pointer;
}
.excel {
    width: 100%;
    table-layout: fixed;
    border-collapse: collapse;
}
.excel tbody {
    display: block;
    width: 100%;
    max-height: 300px;
    overflow-y: scroll;
}
.excel thead,
.excel tbody tr {
    display: table;
    width: 100%;
    table-layout: fixed;
}
.excel .tr {
    width: 100%;
}
.excel th,
.excel td {
    padding: 15px 30px;
    text-align: center;
    border: 1px solid #666;
}
.excel th {
    background-color: #000;
}
.excel tr:nth-child(odd) {
    background-color: #222;
}
.excel tr:nth-child(even) {
    background-color: #333;
}
.excel tr:hover {
    background-color: #111;
}
.btnBox {
    display: flex;
    justify-content: center;
    margin: 40px 0;
}
.btnBox > * {
    width: fit-content;
    height: 40px;
    line-height: 38px;
    padding: 0 40px;
    font-weight: bold;
    border-radius: 20px;
    cursor: pointer;
    transition: all 0.3s;
}
.btnBox .upload {
    color: #222;
    background-color: #eee;
    margin-right: 50px;
}
.btnBox .upload:hover {
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 1) inset;
}
.btnBox .shot {
    color: #eee;
    background-color: #222;
    border: 1px solid #2f2f2f;
}
.btnBox .shot:hover {
    box-shadow: 0 0 5px 0 rgba(255, 255, 255, 1) inset;
}
.output {
    padding: 10px;
    background-color: #222;
    border: 1px solid #4a4a4a;
}
.output .label {
    margin-bottom: 10px;
    font-weight: bold;
    font-family: 'Times New Roman', Times, serif;
}
.output .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 50px;
    padding: 10px;
    background-color: #333333;
    border-radius: 5px;
}
